import React, { createRef } from "react";
import { PlusOutlined } from "@ant-design/icons";
import i18n from "../../i18n";
import {
  getRequest,
  postRequest,
  putRequest,
} from "../../helpers/superagentAPI";

import {
  Modal,
  Button,
  Form,
  Input,
  Row,
  Col,
  Divider,
  Image,
  InputNumber,
  Checkbox,
  Switch,
} from "antd";
import toBase64 from "../../helpers/toBase64";
import RemoteSelect from "../basic/RemoteSelect";

export default class DBModel extends React.Component {
  constructor(props) {
    super(props);
    this.initialState = () => ({
      id: props.id,
      modelName: props.modelName,
      modelTitle: props.title,
      reloadGrid: props.reloadGrid,
      buttonIcon: props.buttonIcon,
      model: null,
      isModalVisible: false,
      plans: [],
    });
    this.state = this.initialState();
    this.loadModel = async () => {
      if (this.state.id) {
        let data = await getRequest(`${this.state.modelName}/${this.state.id}`);
        this.setState(
          {
            model: data,
          },
          () => {
            if (this.modelFormRef.current) {
              this.modelFormRef.current.setFieldsValue({
                ...data,
              });
            }
            this.setPlans(data.plans);
          }
        );
      }
    };

    this.onFormFinished = async (values) => {
      const { id, modelName, reloadGrid } = this.state;
      const {
        headerEn,
        headerAr,
      } = values;
      let form = {
        headerEn,
        headerAr,
        plans: this.state.plans,
        isWithoutSubService: 1,
        id: id ? id : undefined,
      };
      await postRequest(modelName, form);
      if (reloadGrid) reloadGrid();
      this.closeModal();
    };

    this.showModal = () => {
      this.setState(
        {
          isModalVisible: true,
        },
        async () => {
          if (this.state.id) await this.loadModel();
        }
      );
    };

    this.closeModal = () => {
      this.setState({
        isModalVisible: false,
      });
    };

    this.setPlans = async (data) => {
      let plans = await getRequest(
          `SubServicePlan?pageSize=1000&page=0&withoutSubService=1&benefitId=${this.state.id}`
      );
      plans = plans.data.map(d=>{
        return {
          id: d.id,
          headerEn: d.headerEn,
          isIncluded: d.SubServiceBenefite.length != 0? d.SubServiceBenefite[0].isIncluded : false,
          text: d.SubServiceBenefite.length != 0? d.SubServiceBenefite[0].text : '',
        }
      })
      this.setState({
        plans: plans.map((d) => {
          return {
            ...d,
            isIncluded: d.isIncluded === undefined ? 0 : d.isIncluded,
          };
        }),
      });
    };

    this.modelFormRef = createRef();
  }

  render() {
    const { modelTitle, buttonIcon, plans, id } = this.state;
    return (
      <div style={{ width: "100%" }}>
        <Button
          type="primary"
          onClick={this.showModal}
          icon={buttonIcon}
          shape="round"
          className="btn_hover darkBlueBg"
          style={{ width: "100%" }}
        ></Button>
        {this.state.isModalVisible && (
          <Modal
            title={modelTitle}
            visible={this.state.isModalVisible}
            onCancel={this.closeModal}
            width={"85%"}
            okButtonProps={{
              form: "modelForm",
              htmlType: "submit",
              className: "darkBlueBg",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
          >
            <Form
              id={"modelForm"}
              ref={this.modelFormRef}
              onFinish={this.onFormFinished}
              style={{ marginTop: "5%" }}
            >
              <Row gutter={10}>
                <Col span={24}>
                  <Form.Item
                    name={"headerEn"}
                    label={i18n.t("Title English")}
                    rules={[
                      {
                        required: true,
                        message: i18n.t("Input is required"),
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                </Col>

                <Col span={24}>
                  <Form.Item
                    name={"headerAr"}
                    label={i18n.t("Title Arabic")}
                    rules={[
                      {
                        required: true,
                        message: i18n.t("Input is required"),
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                </Col>
              </Row>
            </Form>
            {plans.length != 0 && (
              <table
                style={{
                  width: "100%",
                }}
              >
                <thead>
                  <tr>
                    <th>Plan Name</th>
                    <th>Include</th>
                  </tr>
                </thead>
                <tbody>
                  {plans.map((p, index) => (
                    <>
                      <tr key={index}>
                        <td>{p.headerEn}</td>
                        <td>
                          <Switch
                            checked={p.isIncluded}
                            onChange={(value) => {
                              let newPlans = [...this.state.plans];
                              newPlans[index].isIncluded = value ? 1 : 0;
                              this.setState({
                                plans: newPlans,
                              });
                            }}
                          />
                        </td>
                        <td>
                          <Input
                            value={p.text}
                            onChange={(value) => {
                              let newPlans = [...this.state.plans];
                              newPlans[index].text = value.target.value;
                              this.setState({
                                plans: newPlans,
                              });
                            }}
                          />
                        </td>
                      </tr>
                    </>
                  ))}
                </tbody>
              </table>
            )}
          </Modal>
        )}
      </div>
    );
  }
}
